import React, { useState } from "react";
import { createBoard } from "../../api/fetchBoard";
import { Container, Table, FormGroup, Input, Button } from "reactstrap";
import { Link } from "react-router-dom";
import { useRecoilValue } from "recoil";
import { memberState } from "../../recoil/atom";

const BoardWrite = () => {
  const loggedInMember = useRecoilValue(memberState); // Recoil  

  const [newBoard, setNewBoard] = useState({
    title: "",
    content: "",
    nickname: "",
  });

  const handleCreateBoard = async () => {
    await createBoard(newBoard);
  };

  return (
    <Container className='mt-5'>
      <h1 className='mb-4'> </h1>
      <Table bordered>
        <tbody>
          <tr>
            <th scope='row'></th>
            <td>
              <FormGroup>
                <Input
                  type='text'
                  placeholder='Title'
                  value={newBoard.title}
                  onChange={(e) => setNewBoard({ ...newBoard, title: e.target.value })}
                />
              </FormGroup>
            </td>
          </tr>
          <tr>
            <th scope='row'></th>
            <td>
              <FormGroup>
                <Input
                  type='textarea'
                  placeholder='Content'
                  value={newBoard.content}
                  onChange={(e) => setNewBoard({ ...newBoard, content: e.target.value })}
                />
              </FormGroup>
            </td>
          </tr>
        </tbody>
      </Table>
      <Button color='primary' onClick={handleCreateBoard}>
        
      </Button>
      &nbsp;
      <Link to='/board/list'>
        <Button color='primary'></Button>
      </Link>
    </Container>
  );
};

export default BoardWrite;
